<template>
    <div class="mw-page page" v-nav="{hideNavbar:true}">
        <div class="mw-page-header">
            <von-header>
                <button class="button button-icon ion-ios-arrow-back" slot="left" @click="$router.go(-1)"></button>
                <span slot="title">应用列表</span>
                <button v-if="action==='check'" class="button button-icon" slot="right" @click="action='edit'">编辑</button>
                <button v-if="action==='edit'" class="button button-icon" slot="right" @click="ok()">完成</button>
            </von-header>
        </div>
        <div class="mw-page-content" style="display: flex;flex-direction: column">
          <div class="my-apps" v-if="action==='check'">
            <span >我的应用</span>
            <div class="my-apps-icons">
              <i v-for="item in myApps" :class="['icon', 'iconfont',item.icon ]"></i>
              <i class="icon ion-android-add"></i>
            </div>
          </div>
          <div class="modules" v-show="action==='edit'">
            <p style="margin: 5px">我的应用 <span style="font-size: 12px;opacity: .5">(首页最多放7个应用)</span></p>
            <ul>
              <li v-for="item in myApps" :class="{edit:action==='edit'}">
                <i :class="['icon' ,'iconfont', item.icon]"></i>
                <p>{{item.name}}</p>
                <span class="badge" @click.prevent="removeMyApps(item)"><i class="icon ion-android-remove-circle"></i></span>
              </li>
            </ul>
          </div>
          <div style="flex: 1;overflow-y: auto">
            <div class="modules" v-for="item in apps">
              <p style="margin: 5px">{{item.name}}</p>
              <ul>
                <li :class="{edit:action==='edit'}" v-for="app in item.apps" @click.prevent="navigateTo(app)">
                  <i :class="['icon','iconfont',app.icon]"></i>
                  <p>{{app.name}}</p>
                  <span class="badge" @click.prevent="addApp(app)" v-show="action==='edit'&&!isChecked(app)"><i class="icon ion-android-add-circle add"></i></span>
                  <span class="badge"  v-show="action==='edit'&&isChecked(app)"><i class="icon ion-checkmark-circled checked"></i></span>
                </li>
              </ul>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
  import storage from '@/utils/storage'
    export default{
      name:'apps',
      data(){
        return {
          action:'check',
          myApps:[
            {name:'报价核算单','icon':'icon-hesuandan','path':'/customerOrder/compute'},
            {name:'报价单','icon':'icon-baojiadan','path':'/customerOrder/quote/list',},
            {name:'客户定单','icon':'icon-kehudingdan','path':'/customerOrder/order/list',},
            {name:'生产管理','icon':'icon-shengchanguanli','path':'/customerOrder/manage/list',},
            {name:'车间作业','icon':'icon-chejianzuoye','path':'/customerOrder/compute',},
            {name:'质检','icon':'icon-zhijian','path':'/customerOrder/compute',},
            {name:'库存管理','icon':'icon-kucunguanli','path':'/customerOrder/compute',}
          ],
          apps:[
            {
              name:'客户定单',
              apps:[
                {name:'报价核算单','icon':'icon-hesuandan','path':'/customerOrder/compute'},
                {name:'报价单','icon':'icon-baojiadan','path':'/customerOrder/quote/list'},
                {name:'客户定单','icon':'icon-kehudingdan','path':'/customerOrder/order/list'},
                {name:'定单管理','icon':'icon-hesuandan','path':'/customerOrder/manage/list'},
              ]
            },
            {
              name:'物料管理',
              apps:[
                {name:'BOM列表','icon':'icon-hesuandan','path':'/mats/bom/list'},
              ]
            },
            {
              name:'采购管理',
              apps:[
                {name:'请购单','icon':'icon-hesuandan','path':'/purchase/requisitions'},
                {name:'询价单','icon':'icon-hesuandan','path':'/purchase/inquiry'},
                {name:'采购单','icon':'icon-hesuandan','path':'/purchase/purchase'},
                {name:'物料需求计划','icon':'icon-hesuandan','path':'/purchase/requirements'},
//                {name:'销售定单','icon':'icon-hesuandan','path':'/purchase/sales'},
              ]
            },
            {
              name:'仓库管理',
              apps:[
                {name:'基础数据','icon':'icon-hesuandan','path':'/storage/baseInfo'},
                {name:'领料单','icon':'icon-hesuandan','path':'/storage/applyMatBill/list'},
                {name:'仓库盘点','icon':'icon-hesuandan','path':'/storage/inventory/list'},
                {name:'入库单','icon':'icon-hesuandan','path':'/storage/storagePush/list'},
                {name:'出库单','icon':'icon-hesuandan','path':'/storage/storagePull/list'},
                {name:'物料报废','icon':'icon-hesuandan','path':'/storage/scrap/list'},
                {name:'库存物料管理','icon':'icon-hesuandan','path':'/storage/management/new/new'},
                {name:'凭证管理','icon':'icon-hesuandan','path':'/storage/voucher/list'},
              ]
            },
	    {
              name:'生产管理',
              apps:[
                {name:'工作日历','icon':'icon-hesuandan','path':'/production/shopCalendar'},
                {name:'人员设备','icon':'icon-hesuandan','path':'/production/equipment'},
              ]
            },
//            {
//              name:'采购管理',equipment
//              apps:[
//                {name:'采购管理','icon':'icon-hesuandan','path':'/customerOrder/compute'},
//                {name:'采购管理1','icon':'icon-hesuandan','path':'/customerOrder/compute'},
//                {name:'采购管理2','icon':'icon-hesuandan','path':'/customerOrder/compute'},
//                {name:'采购管理3','icon':'icon-hesuandan','path':'/customerOrder/compute'},
//                {name:'采购管理4','icon':'icon-hesuandan','path':'/customerOrder/compute'},
//              ]
//            },

          ]
        }
      },
      methods: {
        isChecked(item){
         for(let i in this.myApps){
           if(this.myApps[i].name===item.name){
               return true
           }
         }
          return false
        },
        removeMyApps(item){
          this.myApps.splice(this.myApps.indexOf(item),1)
        },
        addApp(item){
          if(this.myApps.length===7){
            $toast.show('首页最多能放7个应用')
            return
          }
          this.myApps.push(item)
        },
        navigateTo(item){
          if(this.action==='edit'){
              return
          }
          this.$router.forward(item.path)
        },
        ok(){
          this.action = 'check'
          storage.remove('myApps')
          storage.set('myApps',JSON.stringify(this.myApps))
        }
      },
      created(){
        if(storage.get('myApps')){
          this.myApps = JSON.parse(storage.get('myApps'))
        }
      },

    }
</script>

<style lang="scss" scoped>



.my-apps{
  height: 50px;background-color: white;padding: 10px;display: flex;
  span{
    line-height: 30px;
  }
  .my-apps-icons{
    line-height: 30px;flex: 1;padding-left: 20px;
    i{
      margin-right: 10px;color: #416eb6;font-size: 16px;
    }
  }
}
.modules{
  background-color: white;
  margin-top: 10px;
  padding: 10px;
  ul{
    display: flex;
    flex-wrap: wrap;
    li{
      text-align: center;
      flex: 0 0 25%;
      height: 80px;
      padding-top: 20px;
      position: relative;
      border: 4px solid white;
      &.edit{
        background-color: #f3f7fb;
      }

      i{
        color: #416eb6;
        font-size: 2em;
      }
      p{
        font-size: 12px;
        padding-top: 5px;
      }
      .badge{
        position: absolute;
        right: 0;
        top: 0;
        .icon{
          font-size: 16px;
          color: red;
        }
        .add{
          color: #416eb6;
        }
        .checked{
          color: #ababab;
        }
      }
    }
  }
}


</style>



